@charset 'utf-8';

a.card {
  border: none;

  .card-body {
    background: $white;
    border: 1px solid $gray-200;
    z-index: 1;
    transition: border 0.3s;
  }

  .card-title {
    transition: 0.4s ease;
  }

  &:hover {
    color: $black;

    .card-body {
      border-color: $black;
    }

    .card-title {
      color: $epflred;
    }
  }
}

.card {
  .card-img-top + .card-body {
    border-top: none !important;
  }
}

a.card-img-top {
  position: relative;
  height: 100%;

  &:before {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: inset 0 0 0 6px $epflred;
    opacity: 0;
    transition: opacity 0.2s;
  }

  &:hover:before {
    opacity: 0.6;
  }
}

a.card-title {
  transition: color 0.3s;
  @include pretty-link($epflred, $white, false, 1px);
  background-position: 0 90% !important;

  &:hover {
    @include pretty-link($black, $white, false, 1px);
  }
}
